@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@100;200;300;400;500;600;700;800;900&display=swap');

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Outfit', sans-serif;

}

body {
  background: white;

}

h1, h4, p,
a{
  color: black;
  text-decoration: none;
}

ul {
  list-style-type: none;
}

.btn {
  padding: 12px 34px;
  font-size: 1rem;
  text-transform: uppercase;
  background: #21a8af;
  color: white;
  border: 2px solid white;
  font-weight: 600;
  cursor: pointer;
}

.btn-light {
  padding: 12px 32px;
  font-size: 1rem;
  text-transform: uppercase;
  background: transparent;
  color: white;
  font-weight: 600;
  border: 2px solid white;
  cursor: pointer;
}

.btn:hover {
  background: grey;
  color: white;
  transition: 0.3s;
}

.btn-light:hover {
  background: #537071;
  color: white;
  transition: 0.3s;
}